.main {
  width: 100%;
  height: 100vh;
  /* background-image: url(../assets/images/bg.jpg); */
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #3b3e997c;
}

.login {
  width: 500px;
  height: 500px;
  border-radius: 20px;
  background-color: #ffffff;
  border: 1px solid black;

}
.logo {
  width: 100%;
  height: 50px;
  position: relative;
  justify-content: center;
  display: flex;
}

.logo img{
    width: 100px;
    height: 100px;
    border: 1px solid black;
    border-radius: 100%;
    position: absolute;
    bottom: 10px;
    background-color:white;
}
.login h1{
    font-weight: 100;
    text-align: center;
}
.login .input{
    width:100%;
    height: 200px;
    padding: 5px 20px;
    box-sizing: border-box;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
    gap: 20px;
    
}
.input input{
    height: 40px;
    background-color: #d4d2d2;
    color: gray;
    border-radius:5px;
    
}
input ::placeholder{
    color: gray;
}
.input button{
    height: 50px;
    background-color: #3b3e997c;
    color: white;
    cursor: pointer;
    border: none;
    border-radius:8px;
    color: black;
    font-size: 25px;
}
 .gbutton{
    margin: 10px 0 0 130px;
    width: 240px;
    height: 50px;
    background-color: #2684fc;
    background-image: url("https://space.clicky.co.uk/resources/images/misc/sign_in_google.png");
    background-repeat: no-repeat;
    background-size: contain;
    cursor: pointer;
}
